.nav {
	&__list {
		display: flex;

		@include xs {
			display: block;
		}
		
	/*	@include md {
			display: flex;
		}*/
		
	}
 
  
  &__close {
    @include lg {
      display: none;
    }
    
    @include xs {
      display: block;
    }
  }

	&__item {
		&:not(:last-child) {
			margin-right: 25px;

			@include xs {
				margin-right: 0;
			}
		}
	}

	&__link {

		@include xs {
			width: 100%;
			display: inline-block;
			padding: 10px 10%;
			border-bottom: 1px solid #bfbfbf;
			font-size: 19px;
			
			
		}

		&:hover {
			@include xs {
				&::before {
					opacity: 0;
				}
			}

		}

		&--current {
			&::before {
				opacity: 1;
				transition: opacity 0.15s;

				@include xs {
					opacity: 0;
				}
			}

		}
	}

	&--visible {
		left: 0;
		transition: left 0.3s;
	}
}
