.shop-nav{
	display: flex;
	align-items: center;
	
		&__item {
		&:not(:last-child){
		margin-right: 12px;
		}
}
	
	&__link {
		display: inline-block;
		width: 16px;
		height: 16px;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		transition: opacity 0.3s;
		
		&:hover {
			opacity: 0.4;
			transition: opacity 0.3s;
		}
		
		&--user {
		background-image: url("../img/user.png");
	}
	
		
	}
	
}

.cart {
	width: auto;
	height: auto;
	
	&__icon {
		display: inline-block;
		width: 16px;
		height: 16px;
		background-image: url("../img/cart.png");
	 background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
	}
}

.search-form {
	&__btn {
		display: inline-block;
		width: 16px;
		height: 16px;
		background-image: url("../img/search.png");
	 background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		transition: opacity 0.3s;
			
		 &:hover {
			opacity: 0.4;
			transition: opacity 0.3s;
		}
	}
}

	.quantity {
		display: inline-flex;
		align-items: center;
		padding: 0 5px;
		border-radius: 25px;
		font-weight: 700;
		font-size: 10px;
		height: 16px;
		color: var(--color-white);
		background-color: var(--color-dark);
	}