.card {
	padding-top: 67px;
}

.card-content {
	padding-top: 20px;
	
	&__bottom {
		padding-top: 70px;
	}
}

.card-top {
	.container {
		display: flex;
    
   @include sm {
     flex-wrap: wrap;
   }
	}
	
	&__left {
		width: 57.5%;
   
   @include sm {
     width: 100%;
   }
	}
	
	&__right {
		padding-top: 57px;
		padding-left: 10.7%;
		width: calc(100% - 57.5%);
   
   @include lg {
     padding-left: 5%;
   }
   
    
   @include sm {
     width: 100%;
     padding-left: 0;
   }
	}
	
}

.card-info {
	&__prop {
		margin-bottom: 16px;
		padding-left: 19px;
	}
	
	&__title {
		margin: 0;
		margin-bottom: 15px;
		font-weight: 500;
		font-size: 24px;
	}
	
	&__rate {
		margin-bottom: 25px;
		font-size: 13px;
		color: var(--color-gray);
		
		img {
			margin-right: 3px;
		}
	}
	&__price {
		margin-bottom: 22px;
	}
	
	
	
	&__descr {
		margin-bottom: 53px;
		font-size: 13px;
		line-height: 20px;
		color: var(--color-tundora);
	}
	
	&__first-select {
		margin-bottom: 28px;
	}
	&__second-select {
		margin-bottom: 82px;
	}
	

	&__stepper {
		margin-bottom: 10px;
	}
	
	&__btn {

		width: 100%;
		height: 58px;
		font-weight: 700;
		font-size: 10px;
		text-transform: uppercase;
		background-color: #f2f2f2;
		color: var(--color-dark);
		margin-bottom: 10px;
		transition: color 0.3s, background-color 0.3s;
		
		&:hover {
		background-color: var(--color-dark);
		color: var(--color-white);
		transition: color 0.3s, background-color 0.3s;
	 }
		
		&--tocart {
		background-color: var(--color-dark);
		color: var(--color-white);
				
			&:hover {
  color: var(--color-dark);
		background-color: #f2f2f2;
		transition: color 0.3s, background-color 0.3s;
	 }
		}
			
	
	}
}
.info-price {
	display: flex;
	align-items: flex-end;
	
	&__current {
		margin-right: 20px;
		font-size: 24px;
		font-weight: 700;
	}
	
	&__old {
	 color: #959595;
		text-decoration: line-through;
		font-size: 16px;
	}

}


.color-select {
	
	&__selected {
		margin: 0;
		margin-bottom: 17px;
		color: var(--color-gray);
		font-size: 10px;
		font-weight: 700;
		text-transform: uppercase;
		
		span {
			color: var(--color-dark);
		}
	}
	&__list {
		display: flex;
		align-items: center;
		padding-left: 4px;
	}
	
	&__item {
		&:not(:last-child){
			margin-right: 12px;
		}
	}
	&__btn {
		position: relative;
		width: 24px;
		height: 24px;
		border-radius: 100%;
		
		&::before {
			content: "";
			position: absolute;
			left: 50%;
			top: 50%;
			border: 1px solid #ccc;
			border-radius: 100%;
			width: 32px;
			height: 32px;
			transform: translate(-50%, -50%);
			transition: border 0.3s;
		}
		&:hover {
			&::before {
				border: 1px solid var(--color-dark);
				transition: border 0.3s;
				
			}
		}
		
		&--active {
			&::before {
				border: 2px solid var(--color-dark);
				transition: border 0.3s;
				
			}
		}
	}
}



.size-select {
	
	&__selected {
		margin: 0;
		margin-bottom: 13px;
		color: var(--color-gray);
		font-size: 10px;
		font-weight: 700;
		text-transform: uppercase;
		
		span {
			color: var(--color-dark);
		}
	}
	
	&__clear {
		display: none;
		color: #d24242;
		font-size: 10px;
		font-weight: 700;
		text-transform: uppercase;
		svg {
			margin-right: 2px;
			width: 8px;
			height: 8px;
			fill: currentColor;
		}
	}
	&__list {
		display: flex;
		align-items: center;
		margin-bottom: 8px;
   
   @include lg {
     flex-wrap: wrap;
   }
		
	}
	
	&__item {
		&:not(:last-child){
			margin-right: 5px;
		}
   
    @include lg {
     margin-bottom: 5px;
   }
	}
	&__btn {
		position: relative;
		width: 42px;
		height: 32px;
		font-size: 13px;
		color: var(--color-gray);
		transition: color 0.3s;
		
		
		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			border: 1px solid #ccc;
		 width: 100%;
			height: 100%;
		 transition: border 0.3s;
		}
		&:hover {
			color: var(--color-dark);
			transition: color 0.3s;
			&::before {
				border: 2px solid var(--color-dark);
				transition: border 0.3s;
				
			}
		}
		
		&--active {
			&::before {
				border: 2px solid var(--color-dark);
				transition: border 0.3s;
				color: var(--color-dark);
			 transition: color 0.3s;
				
			}
		}
	}
}

.stepper {
	display: flex;
	border: 1px solid #ccc;
	height: 58px;
	
	&__btn {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 44px;
		height: 100%;
		
		svg {
			display: block;
			height: 12px;
			width: 12px;
			fill: var(--color-gray);
			transition: fill 0.3s;
		}
		
			&:hover {
			svg {
			fill: var(--color-dark);
			transition: fill 0.3s;
			}
		}
		
		&--disabled {
			opacity: 0.4;
			pointer-events: none;
		}
	}
	
	&__input {
		flex-grow: 1;
		border: none;
		height: 100%;
		color: var(--color-dark);
		text-transform: uppercase;
		font-size: 13px;
		font-weight: 700;
		text-align: center;
		
	}
}

.card-bottom {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	font-weight: 700;
	font-size: 10px;
	text-transform: uppercase;
	margin-top: 50px;
  
  &__item {
    @include md {
      margin-bottom: 5px;  
    }
  }

	&__social {
		display: flex;
		align-items: center;
		margin-top: 15px;
		
		span {
			margin-right: 13px;
		}
   
   @include sm {
     width: 100%;
   }
	}
	span {
		color: var(--color-gray);
	}
}

.card-description {
	display: flex;
	align-items: flex-start;
	
	&__left {
		padding-top: 30px;
		width: 16%;
   
   @include md {
     width: 35%;
   }
   
   @include xs {
    width: 100%;
   }
	}
	
	.simplebar-track.simplebar-vertical {
		width: 3px;
		background-color: #ccc;
	}
	
	.simplebar-scrollbar::before {
		opacity: 1;
		width: 3px;
		background-color: var(--color-dark);
		border-radius: 0;
		left: 0;
		right: 0;
		height: 100%;
	}
	
	&__right {
		width: 84%;
		padding-left: 125px;
		padding-top: 21px;
		padding-right: 120px;
   
   @include md {
     padding-left: 50px;
     padding-right: 50px;
   }
   
   @include xs {
     width: 100%;
     padding-left: 0;
     padding-right: 0;
   }
	}
	
	&__navigation {
		padding: 23px 0;
		max-height: 210px;
		width: 100%;
		
	}
	
	&__item {
		&:not(:last-child){
					margin-bottom: 50px;
		}
	
	
	}
	
	&__link {
		font-size: 15px;
		font-weight: 700;
		color: var(--color-gray);
		transition: color 0.3s;
		
		&:hover {
			color: var(--color-dark);
			transition: color 0.3s;
		}
		
		&--active {
			color: var(--color-dark);
			pointer-events: none;
		}
	}
	
	&__content {
		display: none;
		
		
			p {
			margin-top: 0;
			margin-bottom: 30px;
			font-weight: 500;
			font-size: 13px;
			line-height: 26px;
		}
		
		&--active {
			display: block;
		}
}
  @include xs {
     flex-wrap: wrap;
   }
}

.card-related {
	overflow: hidden;
	padding-top: 40px;

	
	&__title {
		text-align: center;
		font-weight: 500;
		font-size: 24px;
		line-height: 26px;
		margin-bottom: 40px;
	}
	
	.swiper-wrapper {
	display: flex;
	justify-content: center;
    @include xs {
     justify-content: unset;
   }
}
	
	.swiper-slide {
	max-width: 26%;
/*	display: flex;
	justify-content: center;
	align-items: center;
	flex-grow: 0;*/
   
   @include xs {
     max-width: 28%;
   }
}

}







