.breadcrumbs {
	
	&__list {
		display: flex;
		align-items: center;
  padding-top: 36px;
		padding-bottom: 26px;
		border-bottom: 1px solid #eee;
	}
	
	&__item {
		position: relative;
		
		
		
		&:not(:last-child){
			margin-right: 35px;
			&::after {
			content: "/";
			position: absolute;
			right: -20px;
			top: 50%;
		 color: var(--color-gray);
		 font-size: 10px;
		 text-transform: uppercase;
			transform: translateY(-50%);
			
		}
		}
	
	}
	&__link {
	  color: var(--color-gray);
		 font-size: 10px;
		 text-transform: uppercase;
		 transition: color 0.3s;
		
		&:hover {
			color: var(--color-dark);
			transition: color 0.3s;
			}
		&--current {
			color: var(--color-dark);
			
			
		}
	}
}

 .breadcrumbs  .card-prev,
 .breadcrumbs  .card-next {
	 display: none;
		
}

.card {
	
	
	.breadcrumbs .card-prev,
 .breadcrumbs .card-next {
	 display: inline-block;
		
		text-transform: uppercase;
		font-size: 10px;
		font-weight: 700;
		color: var(--color-gray);
		transition: color 0.3s;
		
		&:hover {
			color: var(--color-dark);
			transition: color 0.3s;
		}
}
	
	.breadcrumbs .container {
		display: flex;
		align-items: center;
   
    @include xs {
     flex-wrap: wrap;
   }
	}
	
	.breadcrumbs__list {
		border-bottom: none;
    @include xs {
     width: 100%;
    margin-bottom: 0;
   }
	}
	
.card-prev {
  margin-left: auto;
   @include xs {
     margin-left: 0;
   }
	}
	
	.card-next {
		margin-left: 20px;
	}
	
	
	
}
	