.footer {
/*  
  &__container {
    max-width: 1360px;
  }*/
	
	&__content {
		padding: 98px 40px 0;
		
		@include lg {
			padding: 0 100px;
			padding-top: 98px;
		}
		
		@include md {
			padding: 0;
			padding-top: 80px;
		}

	
	}
	
	&__top {
		display: flex;
		justify-content: space-between;
		padding-bottom: 95px;
	
		@include sm {
			flex-wrap: wrap;
		padding-bottom: 50px;

	}
	
	}
	
	&__list {
		&--row {
			display: flex;
			font-size: 12px;
			
				
				.footer__item {
					margin-bottom: 0;
						&:not(:last-child){
			   margin-right: 43px;
							@include sm {
		     margin-right: 20px;

	      }
		   }
				}
		}
	}
	
	&__select {
		display: flex;
		align-items: flex-end;
		margin-left: 40px;
		
		&-caption {
			margin-right: 14px;
			color: var(--color-gray);
			font-size: 13px;
		
		}
	}
	
	&__copy {
		font-size: 13px;
	}
	
	&__column {
		
		&:nth-child(2){
			margin-left: -60px;
			
			
				@include md {
			margin-left: 0;
		}
		}
		
		&:last-child {
			@include md {
			width: 40%;
		}
			
			@include sm {
				margin-top: 40px;
   	width: 100%;

	 }
		}
	
			&--flex {
			display: flex;
			align-items: flex-start;
		}
	
	}
	
	&__item {
		
		&:not(:last-child){
			margin-bottom: 18px;
		}
	}
	
		&__link {
			position: relative;
		color: var(--color-tundora);
		font-size: 13px;
			
			&::after {
				content: "";
				position: absolute;
				left: 0;
				bottom: 0;
				width: 100%;
				height: 2px;
				background-color: currentColor;
				opacity: 0;
				transition: opacity 0.3s;
				
			}
			
			&:hover {
				&::after {
				opacity: 1;
				transition: opacity 0.3s;
				}
		}
			
			&--row {
		color: var(--color-dark);
				font-weight: 700;
			}
			
						
			@include xs {
				font-size: 12px;
			}
	}
	
	&__title {
		margin: 0;
		margin-bottom: 37px;
		font-size: 16px;
		font-weight: 700;
		text-transform: uppercase;
		
				@include xs {
				font-size: 14px;
			}
	}
	
	&__form {
transform: translateY(3px);
margin-bottom: 25px;
width: 400px;
	
			@include md {
			width: 100%;
		}
	
}
	
	
	&__text {
		color: var(--color-tundora);
		font-size: 13px;
		font-weight: 500;
	}
	
	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items:  unset;
		border-top: 1px solid #eeeeee;
		padding: 40px 0;
		
		
				@include xs {
				flex-direction: column;
    text-align: center;
				padding: 20px 0;
			}
		
		@include sm {
	 justify-content: space-evenly; 
  align-items: unset;
	}
	}
	
		&__left {
				@include xs {
				margin-bottom: 20px;
			}
	}
	
	
	&__social {
		margin-right: 11px;
	}
}


.subscribe-form {
	&__title {
		position: relative;
		margin: 0;
		margin-bottom: 38px;
		font-weight: 500;
		color: var(--color-tundora);
		font-size: 13px;
	}
	
	&__btn {
		position: absolute;
		right: 0;
		bottom: 0;
		z-index: 2;
		padding: 13px 10px;
		color: var(color--dark);
		font-weight: 500;
		font-size: 13px;
		
	}
	
	&__field {
		display: block;
		border-bottom: 2px solid var(--color-dark);
		padding-bottom: 10px;
		width: 100%;
		
		input {
			width: 100%;
			border: none;
		 font-size: 13px;
			padding-right: 80px;
			font-weight: 500;
			color: var(--color-dark);
		

			&::placeholder {
			color: rgba(#454545, 0.5);
			}
		}
	}
}


.social {
	display: flex;
	align-items: center;
	justify-content: center;
	&__item {
		
	}
	
	&__item {
		&:not(:last-child){
			margin-right: 20px;
		}
	}
	
	&__link {
		display: block;
		width: 16px;
		height: 16px;
		background-position: center;
		transition: opacity 0.3s;
		background-repeat: no-repeat;
		
		&:hover {
			opacity: 0.4;
			transition: opacity 0.3s;
		}
		
		&--tw {
			
			background-image: url("../img/kz-twitter.png");
		}
			&--in {
			
			background-image: url("../img/kz-instagram.png");
		}
			&--fb {
			
			background-image: url("../img/kz-facebook.png");
		}
			&--pin {
			
			background-image: url("../img/kz-pinterest.png");
		}
	}
}

.footer--2 {
	.footer__top {
		border-top: 1px solid #eee;
		padding-top: 43px;
		padding-bottom: 19px;
		padding-left: 11px;
   
   @include md {
     flex-wrap: wrap;
   }
		}
  
  .footer__column:first-child {
    @include xs {
      width: 100%;
      margin-bottom: 30px;
      
      .footer__social {
        justify-content: center;
      }
    }
  }
  
  .footer__column:last-child {
    @include md {
      width: 100%;
      margin-top: 30px;
      
      .footer__select{
        margin-left: 0;
        margin-right: 50px;
        
     
      }
    }
    
    @include xs {
        justify-content: center;
      .footer__select{
        margin-left: 0;
        margin-right: 20px;
      }
      .footer__select:last-child {
        margin-right: 0;
      }
    }
  }
	
	.footer__column:nth-child(2){
		margin-left: 200px;
   @include lg {
     margin-left: 0;
   }
   
   @include xs {
     width: 100%;
     .footer__list--row {
       justify-content: center;
     }
   }
	}
	
	
	
	.footer__bottom {
	 border: none;
		padding-left: 5px;
		padding-right: 10px;
		padding-bottom: 20px;
		
				
}
}
	
	


