.catalog-filters {
	width: 309px;
	padding-right: 62px;
	padding-top: 27px;
	margin-right: 30px;
  
  @include md {
    width: 50%;
  }
  @include sm {
    display: none;
    position: absolute;
    padding: 30px 25px;
    width: 100%;
    left: 0;
    top: 90px;
    z-index: 30;
    background: #fff;
  }
}

.catalog-filters--open {
  display: block;
}

.catalog-mobile-filters {
  display: none;
  @include sm {
  
    display: inline-flex;
    align-items: center;
    
    svg {
      width: 20px;
      height: 20px;
      margin-right: 5px;
      
    }
  }
}

.catalog-content {
	min-height: 500px;
	position: relative;
	.container {
		display: flex;
	}
}

.hide-filters {

	font-weight: 700;
	font-size: 10px;
	padding-left: 24px;
	margin-bottom: 18px;
	text-transform: uppercase;
	background-image: url("../img/cross.png");
	background-repeat: no-repeat;
	background-position: left center;
	
}

.catalog-filter {
	border-top: 1px solid #eee;

	&__top {
		display: flex;
		justify-content: space-between;
		align-items:  center;
		padding-top: 34px;
		padding-bottom: 34px;
		cursor: pointer;
		user-select: none;
	}
	
	&__caption {
		display: flex;
		align-items: center;
		
	}
	
	&__title {
		margin: 0;
		display: inline-block;
		font-size: 13px;
		font-weight: 700;
		text-transform: uppercase;
	}
	
	&__quantity {
		margin-left: 5px;
		min-width: 16px;
		transform: translateY(-1px);
	}
	
	&__bottom {
		display: none;
		padding-bottom: 34px;
		

	}
	
&__toggle {
		position: relative;
		width: 12px;
		height: 12px;
		color: #aaa;
		
		&::before {
			content: "";
			position: absolute;
			left: 50%;
			top: 0;
			width: 2px;
			height: 100%;
			background-color: currentColor;
			transform: translateX(-50%);
		}
		&::after {
			content: "";
			position: absolute;
			left: 0;
			top: 50%;
			width: 100%;
			height: 2px;
			background-color: currentColor;
			transform: translateY(-50%);
		}
	}
	
	&__items {
		columns: 2;
	}
	&__item {
		margin-bottom: 14px;
	}
	
	&--open {
		.catalog-filter__bottom {
				display: block;
		}
		
		.catalog-filter__toggle {
			&::before {
				display: none;
			}
		}
	
	}
	
	&:last-child {
		border-bottom: 1px solid #eee;
	}
}

.custom-checkbox {
	display: inline-block;
	cursor: pointer;
	user-select: none;
	&__text {
		position: relative;
		padding-left: 15px;
		color: var(--color-gray);
		font-size: 13px;
		transition: color 0.3s;
		
		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: 3px;
			width: 11px;
			height: 11px;
			border: 2px solid #aaa;
		}
		
		&::after {
			content: "\2713";
		position: absolute;
			left: 2px;
			top: 0;
			color: #aaa;
		}
		
		&:hover {
			color: var(--coloe-dark);
			transition: color 0.3s;
		}
	}
	
	&__input {
		&:checked + span {
			color: var(--color-dark);
		
			&::before {
				border-color: var(--color-dark);
			}
			&::after {
				color: var(--color-dark);
			}
		}
	}
}


.catalog-grid {
	padding-top: 27px;
	width: 987px;
	
	&__content {
		display: flex;
		flex-wrap: wrap;
		padding-top: 20px;
		margin-bottom: 70px;
   
   @include md {
     justify-content: space-between;
   }
	}
	
	&__item {
	
		margin-bottom: 48px;
   @include md {
     width: 47%     !important;
     margin-right: 0 !important;
     margin-bottom: 50px;
   }
   @include xs {
     width: 48%  !important;
   }
	}
}


[data-grid-columns="3"]{
	
	.catalog-grid__item {
		width: 31.3%;
   margin-right: 10px;
		
		&:not(:nth-child(3n)){
			//margin-right: 2%;
		}
		&:nth-last-child(-n+3){
			margin-bottom: 0px;
		}
	}
}


[data-grid-columns="4"]{
	
	.catalog-grid__item {
		width: 22.7%;
		
		&:not(:nth-child(4n)){
			margin-right: 2%;
		}
			&:nth-last-child(-n+4){
			margin-bottom: 0px;
     
     @include md {
       margin-bottom: 50px;
     }
		}
	}
}


[data-grid-columns="5"]{
	
	.catalog-grid__item {
		width: 17.6%;
		
		&:not(:nth-child(5n)){
			margin-right: 2%;
		}
			&:nth-last-child(-n+5){
			margin-bottom: 0px;
		}
	}
}

.catalog-props {
	
	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 26px;
		
		select {
			display: none;
		}
	}
}

.catalog-columns {
	display: flex;
	align-items: center;
	font-weight: 700;
	font-size: 10px;
	text-transform: uppercase;
	
	&__text {
		margin-right: 3px;
	}
	
	&__list {
		display: flex;
		align-items: center;
	}
	
	&__item {
		&:not(:last-child){
			margin-right: 5px;
		}
	}
	
	&__btn {
		padding: 0 5px;
		font-size: 10px;
		color: var(--color-gray);
		
		&:hover {
			color: var(--color-dark);
		}
		
		&--current {
			color: var(--color-dark);
			
			&::before {
				opacity: 1;
			}
			
		}
	}
  
  @include md {
    display: none;
  }
}

.catalog-choice {
	font-size: 0;
	display: none;
	
	&__item {
		border-radius: 12px;
		padding: 5px 12px;
		margin-right: 5px;
		margin-bottom: 5px;
		background-color: #eee;
		color: var(--color-tundora);
		font-weight: 500;
		font-size: 13px;
		transition: background-color 0.3s, color 0.3s;
		
		svg {
			margin-left: 5px;
			width: 8px;
			height: 9px;
			fill: currentColor;
			pointer-events: none;
		}
		
		&:hover {
		background-color: var(--color-dark);
		color: var(--color-white);
		transition: background-color 0.3s, color 0.3s;
		}
	}
		&__clear {
			position: relative;
			font-weight: 500;
			font-size: 13px;
			
				&::before {
			position: absolute;
			content: "";
			left: 0;
		 bottom: -5px;
			width: 100%;
			height: 2px;
			background-color: currentColor;
			opacity: 1;
			transition: opacity 0.15s;
		}
		}
	
}

.custom-select {
	position: relative;
	min-width: 115px;
	user-select: none;
	

	
	&__dropdown {
		display: none;
		position: absolute;
		left: 0;
		top: calc(100% + 2px);
		width: 100%;
		padding: 10px;
		padding-left: 0;
		z-index: 20;
		background-color: var(--color-white);
		
		
	}
	
	&:hover {
		.custom-select__dropdown {
			display: block;
		}
	}
	

	
	&__item {
		cursor: pointer;
		transition: background-color 0.3s;
		&:not(:last-child){
			margin-bottom: 10px;
			white-space: nowrap;
		}
		&:hover {
			background-color: #f1f1f1;
			transition: background-color 0.3s;
		}
	}
	
	&__top {
		position: relative;
		border-bottom: 1px solid #e5e5e7;
		padding-right: 30px;
		padding-bottom: 12px;
		font-size: 10px;
		text-transform: uppercase;
		font-weight: 700;
		cursor: pointer;
		
		&::after {
			content: "";
			position: absolute;
			right: 10px;
			top: 50%;
			width: 10px;
			height: 6px;
			transform: translateY(-50%);
			background-image: url("../img/angle-down.png");
			background-position: center;
			background-repeat: no-repeat;
			margin-top: -6px;
			background-size: cover;
			
		}
	}
	
	&--open {
		.custom-select__top {
			&::after {
				transform: translateY(-50%) rotate(180deg);
			}
		}
		.custom-select__dropdown {
			display: block;
	}
		
		}
	
	&--footer {
		min-width: auto;
		
		
		.custom-select__top {
			border-bottom: none;
			padding-bottom: 0;
			font-size: 13px;
			
			&::after {
				margin-top: auto;
			}
		}
	}
}

.pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	&__item {
		&:not(:last-child){
			margin-right: 8px;
			
		}
	}
	
	&__link {
		display: inline-flex;
		border: 2px solid #eee;
		align-items: center;
		color: var(--color-gray);
		font-size: 10px;
		height: 40px;
		padding: 0 16px;
		font-weight: 700;
		text-transform: uppercase;
		transition: color 0.3s, background-color 0.3s, border 0.3s;
		
		svg {
			margin-left: 3px;
			width: 7px;
			height: 16px;
			fill: currentColor;
		}
		
		&:hover {
			border: 2px solid var(--color-dark);
			background-color: var(--color-dark);
			color: var(--color-white);
			transition: color 0.3s, background-color 0.3s, border 0.3s;
		}
		&--current {
			border: 2px solid var(--color-dark);
			background-color: var(--color-dark);
			color: var(--color-white);
		}
	}
}
