.hero {
	position: relative;
	height: 100vh;
	min-height: 700px;
	background-color: var(--quill-grey);
	
	&::before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: (var(--color-black), 0.05);
	}
	
	&__container {
		position: relative;
		height: 100%;
	
	}
	
	&__title {
		margin: 0;
		max-width: 560px;
		font-weight: 700;
		font-size: 65px;
	 margin-bottom: 17px;
	
			@include md {
			max-width: 500px;
				font-size: 50px;
		}
		
		
				@include xs {
				font-size: 40px;
			}
	
	}
	
	
	
	&__descr {
		margin: 0;
		font-size: 18px;
	
		@include md {
	font-size: 14px;
		}
	
	
	}
	&__image {
		display: inline-block;
		width: 730px;
		height: 653px;
	 background-image: url("../img/girl.png");
		background-position: right;
		background-repeat: no-repeat;
		background-size: contain;
		position: absolute;
		right: 191px;
		bottom: 0;
		max-height: 90%;
		width: 40%;
		
		@include lg {
			right: 50px;
			width: 480px;
			bottom: -30px
		}
		
			@include md {
		  right: 0;
	  	width: 350px;
		}
		
		@include sm {
		font-size: 28px;
   display: none;
	}
	}
	
	&__content {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: center;
		padding: 50px 100px;
		height: 100%;
		
		@include lg {
			padding: 0 100px;
	
		}
			
		@include sm {
			padding: 0 40px;
		
		}
	}
	
	@include lg {
			height: 700px;
		}
	
			@include md {
			padding: 0;
			height: 600px;
			min-height: auto;
		}
	
	
}

.banner-slider {
	width: 100%;
	height: 100%;
	overflow: hidden;
	.swiper-slide {
		
		height: 100%;
		
	}
}

 .hero-pag {
  left: 50%;
  bottom: 46px;
  //transform: translateX(-50%);
  
  .swiper-pagination-bullet {
    margin: 0 12px;
    width: 6px;
    height: 6px;
    background-color: var(--color-white);
    opacity: 0.5;
  }
  .swiper-pagination-bullet-active {
    position: relative;
    background-color: var(--color-dark);
    opacity: 1;
  }
  
  &::before {
    content: "";
    position: absolute;
    left: 50px;
    top: 50px;
    z-index: -1;
    border-radius: 100%;
    width: 20px;
    height: 20px;
    background-color: var(--color-white);
    transform: translate(-50%, -50%);
  }
}

.marketing {
	width: 280px;
	display: flex;
	position: absolute;
	z-index: 10;
	left: 50px;
	bottom: 20px;
	padding: 10px;
	box-shadow: 0 5px,30px rgba(0, 0, 0, 0.1);
	background-color: var(--color-white);
	transform: translateX(-150vw);
	transition: transform 0.3s;
	
	&--visible {
		transform: translateX(0);
		transition: transform 0.3s;

	}
	
	&__text {
		margin-left: 10px;
	}
	
	&__descr {
		display: block;
		margin-bottom: 5px;
		color: var(--color-gray);
		font-size: 11px;
	}
	&__title {
		margin: 0;
		margin-bottom: 7px;
		font-size: 15px;
		min-height: 34px;
		padding-right: 20px;
		
		
	}
	
	&__image {
		flex-shrink: 0;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	
	&__when-from {
		display: block;
		color: var(--color-gray);
		font-size: 11px;
		padding-right: 40px;
	}
	
	&__close {
		position: absolute;
		right: 10px;
		top: 10px;
		padding: 0;
		width: 10px;
		height: 10px;
		background-image: url("../img/close.svg");
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		
	}
		@include xs {
			left: 15px;
			}
}
