

.header {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 100;
	
	&__container {
		padding-top: 25px;
		padding-bottom: 25px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		
		@include xs {
			padding: 20px 15px;
		}
	}
	&__shop-nav {
		margin-left: 150px;
		
		@include md {
			margin-left: 0;
		}
	}
}

.logo {
	display: inline-block;
	width: 117px;
	height: 22px;
	svg {
	 display: block;
		width: 70%;
		height: 70%;
		object-fit: cover;
		fill: var(--color-dark);
		
		@include xs {
		width: 90%;
		height: 80%;
		}
	}
	
	

}

.catalog-header {
	padding-top: 38px;
  
 @include xs {
	 padding-top: 0;
}
}

.free-delivery {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 120;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 40px;
	color: var(--color-white);
	background-color: rgba(0,0,0,0.5);
	font-size: 10px;
	text-transform: uppercase;
	
	&__btn {
		position: absolute;
		right: 40px;
		top: 50%;
		width: 12px;
		height: 12px;
		transform: translateY(-50%);
   

		
		svg {
			display: block;
			width: 100%;
			height: 100%;
			fill: var(--color-white);
			
		}
	}
	 @include xs {
	  display: none;
}
}

.card-page {
	.header {
		padding-top: 0;
	}
}

.nav {
	@include md {
		margin-right: 20px;
		text-decoration: none;
  }
  
   &__class {
    @include lg {
      display: none;
    }
     @include xs {
       display: block;
     }
      
  }
	
	
&__close {
			@include xs {
			position: absolute;
				right: 10%;
				top: 4%;
		}
	}
		@include xs {
		 position: fixed;
			padding: 10% 0;
			left: -150vw;
			top: 0;
			width: 80%;
			height: 100vh;
			background-color: #fff;
			transition: left 0.3s;
		}
}

.burger {
	position: relative;
	width: 25px;
	height: 18px;
	display: none;
	
	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
	 width: 100%;
		height: 2px;
	 background-color: var(--color-dark);
}
	
		
	&::after {
		content: "";
		position: absolute;
		left: 0;
		bottom: 0;
	 width: 100%;
		height: 2px;
	 background-color: var(--color-dark);
}
	
	&__line {
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
	 width: 100%;
		height: 2px;
		background-color: var(--color-dark);
		
	}

	@include xs {
	
		display: block;
		
	}
}










