.banners {
	&__container {

		padding-top: 50px;
		padding-bottom: 30px;
		
		@include xs {
		padding-bottom: 0;
	}
}
	
	&__list {
		display: flex;
		
			@include md {
			flex-wrap: wrap;
		}
	}
	&__item {
		flex-shrink: 0;
		width: 33%;
		
		&:not(:last-child){
				margin-right: 0.6%;
		}
		
			@include md {
				margin-bottom: 20px;
		  width: 100%;
				margin-right: 0;
		}
	
	}
	
	&__title {
		font-size: 36px;
		margin: 0;
		margin-bottom: 20px;
	}
}

.banners-article {
	padding: 54px 40px;
	padding-right: 30%;
 background-color: #e9e4fd;
	min-height: 290px;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	
	&--midseason {
		
		background-image: url("../img/midseason.jpg");
	
	}
	
		&--summer {
	
		background-image: url("../img/summer.jpg");
	}
		&--accs {
		
		background-image: url("../img/accs.jpg");
	}
	
	@include md {
		padding: 50px 20px;

	}
	
	@include sm {
	padding-right: 150px;

	}

}