.product {
	position: relative;

	&__image {
 /* height: 450px;*/
		overflow: hidden;
		margin-bottom: 20px;
		position: relative;
 
   @include xs {
	  height: auto;
  }
   
		img {
		 width: 100%;
			max-height: 100%;
			object-fit: cover;
			transition: transform 0.3;
		}
	}
	&__sale {
		position: absolute;
		left: 20px;
		top: 20px;
		border: 2px solid var(--color-dark);
		padding: 3px 10px;
		font-weight: 700;
		color: #111;
		text-transform: uppercase;
		font-size: 10px;
		z-index: 20;
	}
	
	&__prop {
		
		margin-bottom: 10px;

		
	}
	
	&__title {
		margin: 0;
		margin-bottom: 10px;
		color: var(--color-tundora);
		font-size: 13px;
		
		a {
			&::before {
				content: "";
				position: absolute;
				left: 0;
				right: 0;
				bottom: 0;
				top: 0;
			}
		}
	}
	&__price {
		font-weight: 700;
		font-size: 13px;
	}
	
	&__oldprice {
		margin-left: 5px;
		font-weight: 700;
		font-size: 13px;
		color: #959595;
		text-decoration: line-through;
	}
	&:hover {
		.product__image {
			img {
				transform: scale(1.1);
				transition: transform 0.3s;
			}
		}
		.product-custom__price {
			transform: translateY(-100%);
			transition: transform 0.3s;
		}
		.product-custom__cart {
			transform: translateY(-100%);
			transition: transform 0.3s;
		}
	}
}


.new  {
	background-image: url("../img/new.png");
	
}

.product-prop {
	 display: inline-block;
		font-weight: 700;
		font-size: 10px;
		text-transform: uppercase;
		color: #111;
		padding-left: 16px;
		background-position: left center;
		background-repeat: no-repeat;
}

.product-custom {
	height: 18px;
	overflow: hidden;
	
	 
	&__price {
		transition: transform 0.3s;
	}
	
	.to-cart {
		text-transform: uppercase;
		font-size: 10px;
		font-weight: 700;
	}
	&__cart {
		text-transform: uppercase;
		transition: transform 0.3s;
	}
}

.best  {
	background-image: url("../img/best.png");
	
}