.card-slider {
	display: flex;
  
  &__thumbs {
     @include xs {
     margin-top: 20px;
       display: flex;
       overflow-x: auto;
   }
  }
	
	&__thumb {
		cursor: pointer;
		
   img {
			pointer-events: none;
		}
		&:not(:last-child){
		margin-bottom: 10px;
		}
    @include xs {
      flex-shrink: 0;
     margin-bottom: 0 !important;
      
      &:not(:last-child){
	    	margin-right: 5px !important;
		  }
   }
		
	}
	&__main {
		width: 686px;
		margin-left: 15px;
		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
    @include xs {
     width: 100%;
    margin-left: 0;
   }
	}
  
   @include xs {
     flex-direction: column-reverse;
   }
}